<template>
  <div class="controls">
    <label>words:
      <input
        v-model="inputText"
        type="text"
        :placeholder="text"
        @input="$emit('valueChange', inputText)"
      >
    </label>
    <load-button @newContent="loadText" />
  </div>
</template>

<script>
import LoadButton from './LoadButton.vue';

export default {
  components: {
    LoadButton,
  },
  props: {
    text: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      inputText: this.text,
    };
  },
  methods: {
    loadText(e) {
      this.inputText = e;
      this.$emit('valueChange', this.inputText);
    },
  },
};
</script>

<style scoped>
.controls {
  background: #efefef;

  height: 10%;
  width: 100%;
  border-top: 1px dashed #2e382e;
  border-bottom: 1px dashed #2e382e;
  padding: 0.5rem 0;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

input[type="text"] {
  padding: 3px;
  margin: 5px 0;
}

load-button {
  margin-left: 2rem;
}
</style>
